	module.exports = {"data":{"markdownRemark":{"html":"<p>학교의 가장 기본적인 힘은 생각의 힘입니다. 학교 이데올로기의 사회에 대한 방사능은 가장 강력한 힘입니다. 일류 대학이되기 위해서는 우선 이데올로기를 가져야하고 사회와 다른 생각을 가져야합니다. 재능은 사회적 영향을 미치는 학교입니다. 푸단의 장점은 아이디어를 창출 할 수 있다는 것입니다.</p>\n<p>진실을 탐구하는 과정에서 가장 힘든 것은 자신이 어떤 명예를 주 었는지 상관없이 자신의 모든 창조물을 부정하고 그들이 잃어버린 것이 무엇이든간에 새로운 아이디어를 추구하는 것입니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0191.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0191.md","frontmatter":{"title":"11월 20일 일요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0191.md absPath of file >>> MarkdownRemark"}}

